.cover {
  position: relative;
  height: 15rem;
  overflow: hidden;
  margin-bottom: 1rem;

  img {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
}

.info {
  margin-bottom: 2rem;
  display: flex;
  align-items: flex-start;
}

.avatar {
  width: 10rem;
}

.desc {
  flex: 1 1 auto;
  padding: 1rem;
}

.name {
  font-size: 2rem;
  line-height: 1;
}

.data {

}

.contacts {
  margin-top: 1rem;
  font-size: 0.9rem;

  h3 {
    margin: 0;
  }

  &__item {
    display: block;
  }
}